<template>
  <div class="login"> 
    <el-card class="box-card">
      <template #header>
        <div class="card-header">
          <h1>Element-Plus后台管理系统</h1>
        </div>  
      </template>
      <!-- 表单内容 -->
      <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="用户名" prop="username">
          <el-input type='text' v-model.number="ruleForm.username" ></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="pass">
          <el-input type="password" v-model.pass="ruleForm.pass" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <div class="link"><a href = "FirstPage">link1</a></div>
  </div>
</template>

<script>
  export default {
    name: 'LoGin',
    props: {    msg: String  },
    data() {
      var checkUsername = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('用户名不能为空'));
        }
        setTimeout(() => {
          if(typeof(value) !='string'){
            callback(new Error('用户名只能由数字、英文字母和下划线组成'));
          } else {
            if (value.length < 6 || value.length > 12  ) {
                            callback(new Error('必须6-12位字符'));
            } else {
              callback();
            }
          }
        }, 1000);
      };

      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.ruleForm.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      
      return {
        ruleForm: {
          pass: '',
          username: ''
        },
        rules: {
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          username: [
            { validator: checkUsername, trigger: 'blur' }
          ]
        }
      }; //return end 
    },

    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log(this.ruleForm.username+' '+this.ruleForm.pass)
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
    }
}
</script>
